body {
  height: 100%;
  background: linear-gradient(0, #202239, #8595AC);
}
::selection {
  background: #CDD4DE;
}
.page-content {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}



